$dark_blue: #152834;

* {
	font-family: "微軟正黑體", serif;
	position: relative;
	box-sizing: border-box;
	padding: 0px;
	margin: 0px; }

html, body {
	width: 100%;
	height: 100%; }

//Fixed icon
#toTop {
	opacity: 0;
	position: fixed;
	bottom: 20px;
	right: 0px;
	z-index: 100;
	// font-size: 35px
	// color: gray
	// border: solid 3px gray
	// border-radius: 5px 0px 0px 5px
	// padding: 0px 12px
	// transition-duration: 0.2s
 }	// box-shadow: 0px 0px 20px rgba(black, 0.8)

#toTop:hover {
	background-color: gray;
	color: white; }

#toTop.notTop {
	opacity: 1;
	font-size: 35px;
	color: gray;
	border: solid 3px gray;
	border-radius: 5px 0px 0px 5px;
	padding: 0px 12px;
	transition-duration: 0.2s;
	box-shadow: 0px 0px 20px rgba(black, 0.8); }


//HEADER--------------------------------------
header {
	overflow: hidden;
	background-image: url(../images/header_picture_5.jpg);
	background-attachment: fixed;
	background-color: rgba(black, 0.6);
	background-blend-mode: overlay;
	background-position: center center;
	background-repeat: no-repeat;
	user-select: none;
	box-shadow: 0px 0px 20px rgba(black, 0.8); }



#title {
	left: 50%;
	transform: translateX(-50%);
	top: 30%;
	width: 700px;

	@media screen and (max-width: 1024px) and (min-width: 768px) {
		width: 85%;
		top: 40%; }
	@media screen and (max-width: 767px) {
		width: 60%;
		top: 40%; } }


//INTRO SECTION------------------------------------------
.intro_section {
	height: 500px;
	width: 800px;
	margin-left: auto;
	margin-right: auto;
	text-align: center;

	@media screen and (max-width: 768px) {
		width: 100%;
		padding-left: 15px;
		padding-right: 15px; }


	#intro_title {
		margin-top: 60px;
		letter-spacing: 3px;
		font-family: 'Merriweather', serif; }

	#intro_title:after {
		content: " ";
		display: block;
		width: 50px;
		margin-left: auto;
		margin-right: auto;
		height: 3px;
		margin-top: 18px;
		margin-bottom: 40px;
		background-color: $dark_blue; }

	#intro_descrip:after {
		content: " ";
		display: block;
		width: 10px;
		height: 10px;
		background-color: $dark_blue;
		margin-left: auto;
		margin-right: auto;
		margin-top: 170px;
		border-radius: 50%; }

	#more_info {
		text-decoration: none;
		position: absolute;
		border: solid 1px $dark_blue;
		background-color: $dark_blue;
		color: white;
		padding: 8px 20px;
		border-radius: 10px;
		top: 280px;
		left: 50%;
		transform: translateX(-50%);

		@media screen and (max-width: 767px) {
			top: 350px; } } }


//RECOMMEND SECTION---------------------------------------------
#selection_wrap {
	background-color: $dark_blue;
	padding-bottom: 80px;
	padding-top: 15px;

	img {
		width: 500px;
		left: 50%;
		transform: translateX(-50%);

		@media screen and (max-width: 767px) {
			width: 300px; } } }


.recommend_selection {
	border-radius: 10px;
	width: 80%;
	left: 50%;
	transform: translateX(-50%);
	padding-top: 50px;
	text-align: center;
	position: absolute;
	z-index: 4;
	bottom: 250px;
	background-color: white;
	height: 400px;
	box-shadow: 3px 4px 5px 3px rgba(0,0,0,0.3);

	@media screen and (max-width: 1024px) and (min-width: 768px) {
		width: 85%;
		bottom: 200px; }
	@media screen and (max-width: 767px) {
		width: 80%;
		bottom: 200px; } }

.user_select_area {
	z-index: 5;
	position: absolute; }

#recommend_text {
	margin-bottom: 10px; }

.recommend_wrap {
	margin-top: 70px; }

#recommend_hr {
	position: absolute; }





.btn {
	border: solid 2px $dark_blue;
	display: inline-block;
	padding: 5px 20px;
	border-radius: 5px;
	cursor: pointer;
	margin-left: 10px;
	margin-right: 10px;
	transition-duration: 0.2s;
	user-select: none;

	@media screen and (max-width: 767px) {
		margin-bottom: 15px; } }

.btn:hover {
	background-color: $dark_blue;
	color: white; }

.selected {
	background-color: $dark_blue;
	color: white; }

#select_zone_name {
	background-color: $dark_blue;
	text-align: center;
	color: white;
	padding-top: 400px; }



.user_select_area {
	top: 240px;
	left: 50%;
	transform: translateX(-50%);
	font-size: 18px;

	@media screen and (max-width: 767px) {
		top: 280px; }

	.option {
		display: block;
		left: 50%;
		transform: translateX(-50%); }

	select {
		margin-top: 20px;
		width: 280px;
		height: 32px;
		padding-left: 10px;
		font-size: 15px;
		border-radius: 5px;
		cursor: pointer;

		@media screen and (max-width: 767px) {
			width: 250px; } } }


//SHOW SELECTION--------------------------------------------------
.show_selection_wrap {
	background-color: $dark_blue;
	padding-bottom: 100px;

	.show_selection {
		margin-left: auto;
		margin-right: auto;
		justify-content: space-evenly;
		display: flex;
		flex-wrap: wrap;
		width: 1024px;

		@media screen and (max-width: 768px) {
			justify-content: space-evenly;
			width: 100%; } }

	.item_block {
		width: 450px;
		margin-bottom: 60px;
		box-shadow: 3px 4px 5px 3px rgba(0,0,0,0.3);
		background-color: white;
		border-radius: 5px;


		@media screen and (max-width: 1024px) and (min-width: 768px) {
			width: 45%; }

		@media screen and (max-width: 767px) {
			width: 90%; }

		.image_wrap {
			height: 230px;
			overflow: hidden;

			@media screen and (max-width: 768px) {
				height: 190px; } }

		.image_wrap img {
			width: 100%;
			height: 100%;
			border-radius: 5px; }

		.image_wrap .photo:before {
			content: " ";
			position: absolute;
			z-index: 2;
			top: 0;
			left: 0;
			width: 100%;
			height: 101%;
			background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.9) 90%, rgba(0, 0, 0, 1) 100%);
			@media screen and (max-width: 768px) {
				height: 106%; } }


		.title_wrap {
			z-index: 5;
			align-items: flex-end;
			position: absolute;
			display: flex;
			right: 20px;
			left: 20px;
			justify-content: space-between;
			bottom: 15px;
			color: white;
			font-weight: bold;

			.name {
				font-size: 20px; } }

		.content_wrap {
			line-height: 30px;
			padding-top: 25px;
			padding-bottom: 30px;
			padding-left: 25px;
			padding-right: 25px;


			i {
				margin-right: 5px; }

			.fa-map-marker-alt {
				margin-right: 10px;
				margin-left: 2px; }
			.fa-phone {
				margin-right: 10px;
				margin-left: -1px; } } } }

//FOOTER------------------------------
footer {
	height: 100px;
	padding-top: 10px;
	padding-bottom: 10px;

	img {
		height: 80px;
		// position: absolute
		left: 50%;
		transform: translateX(-50%); }

	p {
		text-align: center;
		font-size: 6px;
		margin-bottom: 20px;
		font-family: 'Merriweather', serif;
		color: $dark_blue; } }

